.search-bar {
  display: flex;
  align-items:center;
  width: 99%;
  margin: 8px;
  border: 2px solid #d9d9d9;
  border-radius: 4px;
}

.search-input {
  flex: 1;
  margin-right: 8px; /* Adjust spacing between input and dropdown */
  box-shadow: none;
  border: none; /* Remove the border */
  outline: none; /* Remove the outline */
  padding: 5px; /* Add padding for better appearance */
}
.search-input:active,
.search-input:focus{
  border: 0px solid transparent;
  box-shadow: none;
}

.dropdown-container {
  flex: 0 0 auto; /* Prevent the dropdown from growing */
  margin-right: 4px; /* Adjust spacing between dropdown and search icon */
  box-shadow: none;
  border: none;
  outline: none; /* Remove the outline */
}

.search-icon-container {
  flex: 0 0 auto; /* Prevent the search icon from growing */
  align-self: center;
  height:100%;

}
